import React, { useState, useContext } from "react";
import { NavLink } from "react-router-dom";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Box from "@mui/material/Box";
import ThemeContext from '../context/contexts'


export default function MenuTabs(props) {
  const theme = useContext(ThemeContext);
  const [value, setValue] = useState("one");
  const handleChange = (event: React.SyntheticEvent, newValue: string) => {
    setValue(newValue);
  };

  const tabStyle = {
    color: theme.theme === "dark" ? "var(--primary-white)" : "#454545",
    fontWeight: "light",
    fontSize: ".8rem",
    textTransform: 'none',
    transition: '0.15s ease-in',
    '&:hover': {
      backgroundColor: theme.theme === "dark" ? "#30363d" : "#f1f8ff",
    }
  };
  return (
    <div className="tabs_container">
      <div className="tab-items">
        <Box sx={{ width: "100%", paddingLeft: '10px' }}>
          <Tabs
            value={value}
            onChange={handleChange}
            textColor="transparent"
            indicatorColor="primary"
          >
            <Tab value="one" label="Result" sx={tabStyle} />
            <Tab to="/feedback" value="two" label="&lt;/&gt;&nbsp;Source Code" sx={tabStyle} />
            <Tab value="three" label="Feedback" sx={tabStyle} />
          </Tabs>
        </Box>
      </div>
      <hr style={{border: 'none', backgroundColor: theme.theme === "dark" ? "#30363d" : "#ccc", height: '1px'}} />
    </div>
  );
}

