import React, {useContext} from "react";
import Search from "./Search";
import WordOfTheDay from "./DayWord";
import About from "./About";
import ThemeContext from '../context/contexts'



export default function Result() {
  const theme = useContext(ThemeContext);
  const boxStyle = {
    backgroundColor:  theme.theme === "dark" ? "#0d1117" : "white",
    border: theme.theme === "dark" ? "1px solid #30363d" : "1px solid #ccc",
    borderRadius: "6px",
  };
  return (
    <>
      <div className="container">
        <Search boxStyle={boxStyle} />
        <WordOfTheDay boxStyle={boxStyle} />
        <About boxStyle={boxStyle} />
      </div>
    </>
  );
}
